import React from "react";
import PokemonCard from "./PokemonCard";

const PokemonList = ({ pokemons }) => {
    return (
        <div>
            {pokemons.map((pokemon, i) => {
                return (
                    <PokemonCard pokeName={pokemons[i].name} pokeID={i + 1} />
                );
            })}
        </div>
    );
};

export default PokemonList;
